import { Component, OnInit } from '@angular/core';
import {MovieApiServiceService} from '../movie-api-service.service';
import {RegisterDataService} from '../register-data.service';
import {Router} from '@angular/router';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  loginFlag :boolean= false;
  term:any="";
  movieData:any;
tvData:any;
posterPath: string="https://image.tmdb.org/t/p/w500";
  constructor(private _MovieApiServiceService:MovieApiServiceService, private _RegisterDataService:RegisterDataService, private _Router:Router)
   {
    _RegisterDataService.currentToken.subscribe((data:any)=>{
      if(data === null){
        this.loginFlag=false;
      }
      else{
        this.loginFlag =true;
      }
    })
     _MovieApiServiceService.getMoviesTv("movie").subscribe( (data)=>{
       this.movieData =data.results.slice(0,10);
     })

     _MovieApiServiceService.getMoviesTv("tv").subscribe( (data)=>{
      this.tvData =data.results.slice(10,20);
    })
    }
    // ///////getMovieDetails Func
    getMovieDetails(_id:any){
        this._Router.navigate([`/movieDtails//${_id}/`]);
    }
    getMovieDetailsTv(_id:any){
      this._Router.navigate([`/tvDetails//${_id}/`]);
  }
  ngOnInit(): void {
  }

}
