import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';// to get id from url
import {MovieApiServiceService } from '../movie-api-service.service'
@Component({
  selector: 'app-movie-details',
  templateUrl: './movie-details.component.html',
  styleUrls: ['./movie-details.component.scss']
})
export class MovieDetailsComponent implements OnInit {
movieId:any;
allMovieDetails:any;
posterPath: string="https://image.tmdb.org/t/p/w500";
  constructor(private _ActivatedRoute:ActivatedRoute, private _MovieApiServiceService:MovieApiServiceService) {
        this.movieId=_ActivatedRoute.snapshot.paramMap.get("id");
       _MovieApiServiceService.getDetails(this.movieId).subscribe( (data:any)=>{
        this.allMovieDetails=data;
})
   }

  ngOnInit(): void {
  }

}
