import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import {RegisterDataService} from './register-data.service'
import {Router} from '@angular/router'
@Injectable({
  providedIn: 'root'
})
export class RoutingGuardGuard implements CanActivate {
  constructor(private _RegisterDataService:RegisterDataService, private _Router:Router)
  {}
  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
   if(this._RegisterDataService.currentToken.getValue() != null)
   {
     return true
   }
   else{
    this._Router.navigate(['/login']);
     return false;
   }
  }
  
}
