import { LowerCasePipe } from '@angular/common';
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filterMovies'
})
export class FilterMoviesPipe implements PipeTransform {

  transform(allMovies:any, userWord:string): any {
    return allMovies.filter( function(movie:any){
      return (movie.title.toLowerCase()).includes( userWord.toLowerCase())
    });
  }

}
