import { Component, OnInit } from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {RegisterDataService} from '../register-data.service';
import{Router} from '@angular/router'
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})

export class RegisterComponent implements OnInit {
   MyRegisterForm : any;
  constructor(private _RegisterDataService:RegisterDataService, private _Router:Router) { 

    this.MyRegisterForm = new FormGroup({
      'age':new FormControl(null, [
        Validators.required,
       Validators.min(16)]),
      'email':new FormControl(null,[
        Validators.required,
      Validators.email]),
      'password':new FormControl(null, 
        [Validators.required,
        Validators.minLength(4),
      Validators.maxLength(10)]),
      'first_name':new FormControl(null, [
        Validators.required,
       Validators.minLength(2),
       Validators.maxLength(10), 
       Validators.pattern("^[A-Z a-z]+$")]),
      'last_name':new FormControl(null, [
        Validators.required,
       Validators.minLength(2),
       Validators.maxLength(10), 
       Validators.pattern("^[A-Z a-z]+$")])
    })
  }
  getRegisterData(myForm: FormGroup)
  {
      this._RegisterDataService.sendRegisterData(myForm.value).subscribe((data)=>{
        this._Router.navigate(['/login']);
    })
  }

  ngOnInit(): void {
   
  }

}
