import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';// to get id from url
import {MovieApiServiceService } from '../movie-api-service.service'

@Component({
  selector: 'app-tv-details',
  templateUrl: './tv-details.component.html',
  styleUrls: ['./tv-details.component.scss']
})
export class TvDetailsComponent implements OnInit {
  tvId:any;
  alltvDetails:any;
  posterPath: string="https://image.tmdb.org/t/p/w500";
  
  constructor(private _ActivatedRoute:ActivatedRoute, private _MovieApiServiceService:MovieApiServiceService) {
    this.tvId=_ActivatedRoute.snapshot.paramMap.get("id");
    _MovieApiServiceService.getDetailsTv(this.tvId).subscribe( (data:any)=>{
     this.alltvDetails=data;
})
   }

  ngOnInit(): void {
  }

}
