import { Component, OnInit } from '@angular/core';
import {RegisterDataService} from '../register-data.service';
import {Router} from '@angular/router';
import { BehaviorSubject } from 'rxjs';
@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.scss']
})
export class NavComponent implements OnInit {
loginFlag :boolean= false;
  constructor(private _RegisterDataService:RegisterDataService, private _Router:Router)
   { 
    _RegisterDataService.currentToken.subscribe((data:any)=>{
      if(data === null){
        this.loginFlag=false;
      }
      else{
        this.loginFlag =true;
      }
    })

    if (performance.navigation.type == performance.navigation.TYPE_RELOAD) {
      this._RegisterDataService.currentToken.next(null);
      localStorage.setItem("currentTokenMovies","");
    
    } 
   }
// ////////////////////////////////////LogOut Func
logOut()
{ 
  this._RegisterDataService.currentToken.next(null);
  localStorage.setItem("currentTokenMovies","");
  this._Router.navigate(['/login']);
}
  ngOnInit(): void {
  }

}
