import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { RegisterDataService } from '../register-data.service';
import { Router } from '@angular/router'

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  myLoginForm: any;
  errorMessage: any;
  errorFlage: boolean = false;
  constructor(private _RegisterDataService: RegisterDataService, private _Router: Router) {
    this.myLoginForm = new FormGroup({
      'email': new FormControl(null, Validators.required),
      'password': new FormControl(null, Validators.required)
    }
    )
  }

  getLoginData(myForm: FormGroup) {
    this._RegisterDataService.sendLoginData(myForm.value).subscribe((data) => {
      if (data.message === "success") {
        this.errorFlage = false;
        localStorage.setItem("currentTokenMovies", data.token);
        this._RegisterDataService.saveLoginData(data.token, data.email, data.password, data.first_name, data.last_name);
        this._Router.navigate(['/home']);
      }
      else {
        this.errorFlage = true;
        this.errorMessage = data.message;
      }
      console.log(data);
    })
  }


  ngOnInit(): void {
  }

}
