import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable, BehaviorSubject} from 'rxjs'

@Injectable({
  providedIn: 'root'
})
export class RegisterDataService {
  firstName:any=null;
  lastName:any=null;
  email:any=null;
  password:any=null;
  currentToken:any = new BehaviorSubject(null);
  constructor(private _HttpClient: HttpClient)
   {   
     if(localStorage.getItem("currentTokenMovies") != null)
     {
       this.currentToken.next(localStorage.getItem("currentTokenMovies"));
     }
     else
     {       this.currentToken.next(null);
    }
   }


    sendRegisterData(registerData: any):Observable<any>
    {
         return  this._HttpClient.post("https://routeegypt.herokuapp.com/signup", registerData);
    }

    sendLoginData(loginData: any):Observable<any>
    {

         return  this._HttpClient.post("https://routeegypt.herokuapp.com/signin", loginData);
    }

    saveLoginData(curTok:any,email:string,pass:string,fN:string,lN:string)
    {
      this.firstName =fN;
      this.lastName =lN;
      this.password =pass;
      this.email =email;
      this.currentToken.next(curTok);
    }
    
}
